@use 'base/variables' as v;
@use 'base/mixins' as m;

.dark-theme{
    background-color: v.$dark;
    color: v.$claro;

    p{
        color: v.$gris2;
    }

    .nav{
        background-color: v.$dark;

        .nav-links{
            background-color: v.$dark;
        }

        .menu-icon{
            color: v.$claro !important;
        }

        @include m.tablet{
            .nav-links{
                a{
                    color: v.$claro;
                }
            }
        }
    }

    .contact-item{
        background-color: lighten(v.$dark, 15%);

        input{
            color: v.$claro;
        }        

        input::placeholder{
            color: v.$gris2;
        }

        textarea{
            color: v.$claro;
        }        
        
        textarea::placeholder{
            color: v.$gris2;
        }
    }
}