@use 'base/variables' as v;
@use 'base/mixins' as m;

.projects{
    padding-top: 10rem;

    .cont-projects{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}

.project{
    border: 1px solid v.$color1;
    margin: 1rem;
    max-width: 28rem;
    // box-shadow: 0px 0px 10px 1px v.$color2;

    &:active{
        border: 1px solid v.$color2;

        .project-cont{
            background-color: v.$color2;
        }
    }

    @include m.tablet{
        &:hover{
            border: 1px solid v.$color2;
            scale: 0.985;
            transition: 300ms;
            cursor: pointer;

            .project-img{
                overflow: hidden;
            }

            img{
                scale: 1.1;
                filter:opacity(0.5);
                transition: 300ms;
            }

            .project-cont{
                background-color: v.$color2;
                transition: 150ms;
            }
        }
    }

    .project-img{
        img{
            height: 24rem;
            object-fit: cover;
            width:100%;
        }
    }

    .project-cont{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 2rem 1.5rem;
        background-color: v.$color1;
        color: v.$blanco;
        height: 20rem;

        @include m.tablet{
            height: 18rem;
        }
        

        h3{
            margin: 0;
        }

        p{
            margin: 0;
            color: rgb(255, 255, 255, 0.8);
        }
    }
}