@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav{
    box-shadow: 2px 1px 5px v.$gris;
    position: fixed; 
    width: 100%; 
    top: 0;
    margin-bottom: 10rem;
    z-index: 1;
    background-color: v.$claro;

    @include m.tablet{
        margin-bottom: 15rem;
    }
}

.cont-nav{
    padding: 0.75rem 2rem;
    width: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include m.laptop{
        padding-inline: 0;
    }

    .logo{
        font-size: 4rem;
        color: v.$color1;
        margin-right: 10px;
    }

    .cont-nav-links{
        display: flex;
        align-items: center;
        gap: 20px;
    
        @include m.tablet{
            flex-direction: row-reverse;
        }

        .button-theme{
            background-color: #343d58;
            height: 30px;
            border-radius: 30px;
            border: none;
            position: relative;
            display: flex;
            align-items: center;
            cursor: pointer;
            outline: none;
            padding: 0;

            &::after{
                content: "";
                display: block;
                width: 30px;
                height: 30px;
                position: absolute;
                background-color: #f1f1f1;
                top: 0;
                left: 0;
                right: unset;
                border-radius: 30px;
                transition: 300ms ease all;
                box-shadow: 0px 0px 2px 2px rgb(0, 0, 0, 0.2);
            }

            &.active{
                background-color: v.$color1;

                &::after{
                    right: 0;
                    left: unset;
                }
            }

            span{
                width: 30px;
                height: 30px;
                line-height: 30px;
                display: block;
                background: none;
                color: v.$claro;
            }
        }
    
        .menu{
            background-color: unset;
            padding: 0;
            border: none;
            font-size: 4rem;
            display: block;
            cursor: pointer;
    
            .menu-icon{
                color: v.$gris;

                &:active{
                    color: v.$color1;
                }
            }
    
            @include m.tablet{
                display: none;
            }
        }
    }
    
    .nav-links{
        background-color: v.$color1;
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    
        @include m.tablet{
            background-color: unset;
            color: v.$negro;
            position: relative;
            height: auto;
            align-items: center;
            display: flex !important;
            justify-content: space-around;
        }
    
        a{
            color: v.$claro;
            font-size: 4rem;
            font-weight: 100;
            width: 100%;
            height: 25%;
            display: flex;
            flex-direction: column;            
            justify-content: center;
            align-items: center;    
    
            &:active{
                background-color: v.$color2;
            }
    
            @include m.tablet{
                font-size: 2rem;
                font-weight: 500;
                color: v.$negro;
                margin-inline: 20px;
    
                &:hover{
                    color: v.$color1;
                }
    
                &:active{
                    background-color: unset;
                    color: v.$color2;
                }
            }
        }
    }
}